import React from "react"
import { Link } from "gatsby"
import SEO from "../components/seo"
import Breadcrumbs from "@material-ui/core/Breadcrumbs"
import Grid from "@material-ui/core/Grid"
import "../site.css"

const HPCInfo = () => (
  <div style={{ display: "flex", flex: 1, justifyContent: "center" }}>
    <div className={"hpc"}>
      <SEO
        title="Andrew Stolzle | MyMentor"
        description="mymentor mobile application for Android"
        lang="en"
      />
      <Breadcrumbs aria-label="mymentor breadcrumb">
        <Link
          to="/"
          style={{
            display: "flex",
            color: "#121212",
            fontFamily: `Sen, sans-serif`,
          }}
        >
          Home
        </Link>
        <div
          aria-current="page"
          style={{
            display: "flex",
            color: "#121212",
            fontFamily: `Sen, sans-serif`,
          }}
        >
          HPCBudgeting
        </div>
      </Breadcrumbs>
      <span className={`index-title`}>HPC Budgeting</span>
      <p style={{ fontWeight: 600 }}>
        Development Stack: Angular, Typescript, Material UI, Telerik Kendo UI
        <br />
        <br />
        Other tools: VS Code, Visual Studio
      </p>
      <h3>More info coming soon! For now, here are a couple of screenshots:</h3>
      <Grid container direction="row" justify="space-evenly">
        <figure>
          <img
            src={require("../images/hpc/registration.png")}
            alt={`hpc registration`}
            className={"hpc-img"}
          />
          <figcaption>User registration</figcaption>
        </figure>
        <figure>
          <img
            src={require("../images/hpc/users.png")}
            alt={`hpc user grid`}
            className={"hpc-img"}
          />
          <figcaption>User grid</figcaption>
        </figure>
      </Grid>
    </div>
  </div>
)

export default HPCInfo
